@import "../../styles/utils";

.preloader {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: -100vw;
  visibility: hidden;
  z-index: 2000;

  transition: 1s;

  &_active {
    left: 0;
    visibility: visible;
  }
}

.splash {
  @include preloader-block;

  &_active {
    background-color: #fdfdfd;
  }

  &__spinner {
    position: absolute;
    margin: -25px 0 0 -25px;
    width: 50px;
    height: 50px;
    top: 50%;
    left: 50%;
    z-index: 2;
    animation: rotate 2s linear infinite;

    &-path {
      stroke: hsl(210, 70, 75);
      stroke-linecap: round;
      animation: dash 1.5s ease-in-out infinite;
    }
  }
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}

