@import "../../styles/utils";

.option-button {
  @extend %noBorder;
  @include square($standard-square-size);
  @include box-element;
  @include gap-margins(0, 10px);
  @include normalize-background;
  @include completed(20px, 0, 0);
  @include aborted(20px, 0, 0);

  position: relative;
  background-size: 32px, 32px;
  transition: after 0.5s;

  &_completed {
  }

  &_aborted {
  }

  &_selected {
    background-color: rgba(255, 255, 255, 0.5);
    color: black;
  }

  &_scale, &_preview-scale {
    background-image: url("../../images/admin/edit/scale.png");
  }

  &_position, &_preview-position {
    background-image: url("../../images/admin/edit/position.png");
  }

  &_rotation, &_preview-rotation {
    background-image: url("../../images/admin/edit/rotate.png");
  }

  &_three, &_preview-three {
    background-image: url("../../images/admin/edit/three.png");
  }

  &_save-preview-image {
    background-image: url("../../images/admin/edit/save-preview.png");
  }

  &_face-types {
    background-image: url("../../images/admin/edit/user.png");
    //filter: invert(100%);
  }

  &_glasses-types {
    background-image: url("../../images/admin/edit/glasses-types.png");
  }

  &_sexes {
    background-image: url("../../images/admin/edit/sex.png");
  }
}
