@import "../../styles/utils";

.file-upload {
  &__area {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 30vh;

    @include box-element;
    background: #fdfdfd;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
    border-radius: 14px;

    cursor: pointer;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include gap-margins(10px, 0);
  }

  &__title {
    font-size: 20px;
    font-weight: 400;
    color: #888;
  }

  &__description {
    font-size: 14px;
    color: #888;
  }

  &__errors-dropdown {
  }
}
