@import "../../styles/utils";

.simple-nav {
  display: flex;
  justify-content: center;
  align-items: center;

  &__button {
    @include box-element;
    @include square(40px);
    @include gap-margins(0, 10px);
    @include normalize-background;
    @include noBorder;

    &_previous {
      background-image: url("../../../public/assets/user/navigation/back.png");
    }

    &_next {
      background-image: url("../../../public/assets/user/navigation/back.png");
      transform: scaleX(-1);
    }
  }
}
